.app-customers .people-header {
  display: flex;
  align-items: center;
}
.app-customers .people-header .add-new-people {
  align-items: center;
  display: flex;
  width: 65%;
  margin: 0;
  padding: 0;
}
.app-customers .people-header .add-new-people a {
  background: #1a9ed4;
  border-radius: 3px;
  color: #fff;
  font-size: 12px;
  height: 29px;
  line-height: 29px;
  margin-left: 13px;
  text-align: center;
  text-decoration: none;
  width: 150px;
}
@media (max-width: 782px) and (min-width: 768px) {
.app-customers .people-header .add-new-people a {
    margin-right: 18rem;
    margin-bottom: 3px;
    max-width: 120px;
}
}
@media (max-width: 767px) and (min-width: 707px) {
.app-customers .people-header .add-new-people a {
    margin-right: 16rem;
    margin-bottom: 3px;
}
}
@media (max-width: 706px) and (min-width: 651px) {
.app-customers .people-header .add-new-people a {
    margin-right: 14rem;
    margin-bottom: 3px;
}
}
@media (max-width: 650px) {
.app-customers .people-header .add-new-people a {
    margin-right: 12rem;
    margin-bottom: 3px;
}
}
.app-customers .widefat tfoot td,
.app-customers .widefat tbody th {
  line-height: 2.5em;
}
.app-customers .widefat tbody td {
  line-height: 3em;
}
.app-customers .people-table {
  border-radius: 3px;
}
.app-customers .people-table tbody {
  background: #FAFAFA;
}
.app-customers .people-table th ul,
.app-customers .people-table th li {
  margin: 0;
}
.app-customers .people-table th li {
  display: flex;
  align-items: center;
}
.app-customers .people-table th li img {
  width: 14px;
  padding-right: 5px;
}
.app-customers .people-table .check-column input {
  border-color: #E7E7E7;
  box-shadow: none;
  border-radius: 3px;
}
.app-customers .people-table .check-column input:checked {
  background: #1ABC9C;
  border-color: #1ABC9C;
  border-radius: 3px;
}
.app-customers .people-table .check-column input:checked:before {
  color: #fff;
}
