  padding-left: 20px !important;
  text-align: left !important;
}
.app-customers .check-column {
  padding: 20px !important;
}
@media (max-width: 650px) {
.search-btn {
    display: none;
}
}
@media (max-width: 479px) {
.people-search {
    margin-top: 20px;
}
}
.erp-btn-group {
  display: inline-flex;
  position: absolute;
  right: 17.5rem;
}
.erp-btn-group:after {
  content: "";
  clear: both;
  display: table;
}
@media (max-width: 782px) {
.erp-btn-group {
    right: 17rem;
    margin-top: 23px;
}
}
@media (max-width: 650px) {
.erp-btn-group {
    right: 8.5rem;
}
}
.erp-btn-group button {
  padding: 5px 15px;
  border: 0.3px solid #e2e2e2;
  background-color: #fff;
  color: rgba(0, 0, 0, 0.6);
  font-size: 12px;
  font-weight: 400;
  text-decoration: none;
  line-height: inherit;
  cursor: pointer;
